import React from "react";
import rehypeReact from "rehype-react"
import ExpoSnack from 'react-expo-snack';

import TextLink from "../base/links/text-link";
import { Text } from "../base/typography";
import { Heading } from "../base/typography";
import Image from "../base/image";
import styled from "styled-components";

const ContentWrapper = ({ htmlAst, options }) => {
    const renderAst = new rehypeReact({
        createElement: React.createElement,
        components: {
            h2: ({ node, ...props }) => <Heading {...props} level={2} noMargin />,
            h3: ({ node, ...props }) => <Heading {...props} level={3} noMargin />,
            h4: ({ node, ...props }) => <Heading {...props} level={4} noMargin />,
            h5: ({ node, ...props }) => <Heading {...props} level={5} noMargin />,
            p: ({ node, ...props }) => <Text {...props} $noTopMargin={!options.p.topMargin} />,
            a: ({ node, href, ...props }) => <TextLink to={href} {...props} />,
            img: ({ node, ...props }) => <Image {...props} />,
            "expo-snack": ExpoSnack
        }
    }).Compiler

    return (
        <StyledContentWrapper>
            {renderAst(htmlAst)}
        </StyledContentWrapper>
    )
};

ContentWrapper.defaultProps = {
    options: {
        p: {
            topMargin: true
        }
    }
}

const StyledContentWrapper = styled.article`
    width: 100%;
`

export default ContentWrapper;


